.App {
  /* text-align: center; */
  scroll-behavior: smooth;
  transition: ease 0.5;
}
#home{
  margin-top: 5rem;
  scroll-behavior: smooth;
  margin-left:18rem ;
  padding: 3rem;
  /* border: 1px solid; */
  width:75%;
  display: flex;
  gap: 3rem;
}
#home>div{
  /* border: 1px solid firebrick; */
  text-align: center;
  height: 70%;
  margin: auto;
  font-weight: bold;
}
#home>div>p:nth-child(1){
  display: flex;
  font-size:3pc;
  margin-bottom: 1rem;
}
#home>div>p:nth-child(2){
  font-size:3.3pc;
  color: rgb(49, 130, 206);
  margin-bottom: 1rem;
}
#home>div>p:nth-child(3){
  margin-bottom: 2rem;
  font-size:1.7pc;
}
#home>img{
  width: 35%;
  height:35%;
  /* border: 3px solid; */
  align-items: center;
  border-radius: 50%;
}
#about{
  margin-top: 4rem;
  scroll-behavior: smooth;
  margin-left:20rem ;
  /* border: 1px solid; */
  width:75%;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 1rem;
}
#about>h1{
  color: rgb(49, 130, 206);
  font-weight: bold;
  display: flex;
  /* border: 1px solid; */
  font-size: 3rem;
}
#about>div{
  font-size: 22px;
  /* border: 1px solid; */
  text-align: left;
  padding: 1.5rem;
  letter-spacing: 0.6px;
  line-height: 2.3pc;
}
#skills{
  margin-top: 4rem;
  margin-left:20rem ;
  /* border: 1px solid; */
  width:75%;
  scroll-behavior: smooth;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 1rem;
}
#skills>h1{
  color: rgb(49, 130, 206);
  font-weight: bold;
  /* border: 1px solid; */
  display: flex;
  font-size: 3rem;
}
#skills>div{
  display: grid;
  /* border: 1px solid green; */
  grid-template-columns: repeat(4,1fr);
  justify-content: space-evenly;
}
#skills>div>img{
  width: 80px;
  margin: auto;
  margin-bottom: 2rem;
}
#projects{
  margin-top: 4rem;
  margin-left:20rem ;
  /* border: 1px solid; */
  width:75%;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  gap: 4rem;
  padding: 1rem;
}
#projects h1{
  width: 100%;
  color: rgb(49, 130, 206);
  font-weight: bold;
  display: flex;
  /* border: 1px solid; */
  font-size: 3rem;
}
.project-card{
  /* border: 1px solid; */
  margin: 1rem;
  border-radius: 5px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  /* box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px; */
  padding: 1rem;
}
.project-card>h2{
  text-align: left;
  font-weight: bold;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
.project-card>p{
  font-size: 18px;
  margin: 1rem;
  text-align: left;
}
.project-tech-stack{
/* border: 1px solid greenyellow; */
display: flex;
margin: 1rem;
gap: 5px;
}
.project-tech-stack>p{
  font-size: 1.5pc;
}
.project-tech-stack>img{
  width: 30px;
}
.deployed{
  display: flex;
  gap: 1.5pc;
  margin: 1rem;
  /* border: 1px solid; */
  align-items: center;
}
.github{
  display: flex;
  flex-direction: column;
 align-items: center;
  gap: 10px;
}
.github>img{
  width:70%;
  margin:1rem;
}
#contact{
  margin-top: 4rem;
  margin-left:20rem ;
  scroll-behavior: smooth;
  /* border: 1px solid; */
  width:75%;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  padding: 1rem;
}
#contact>h1{
  width: 100%;
  color: rgb(49, 130, 206);
  font-weight: bold;
  display: flex;
  /* border: 1px solid; */
  font-size: 3rem;
}
#contact>div{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#contact>div>a>img{
width: 35px;
}
#contact>div>a>p{
font-size: 13px;
}